.Chat{text-align:center;height:100vh}.pageStyle{height:100%;display:grid;grid-template-columns:1fr;grid-template-rows:2fr .7fr}

.userComment{padding:.25rem;margin-bottom:.8rem;background-color:white;border-radius:10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.4),0 6px 20px 0 rgba(0,0,0,0.05);display:grid;grid-template-columns:50px 1fr;grid-gap:.5rem}.userComment .commentAvator{width:100%;align-self:center;margin:.25rem;border-radius:50%}.userComment p{margin:.5rem;line-height:1.5}.userComment .commentData{font-size:.75rem}.userComment .commentData *{margin-right:.5rem}.editableComment{background-color:#ffffe0}.editableComment .editDeleteDiv{text-align:right;font-size:small;margin-right:.5rem}.editableComment .editButton:hover,.editableComment .deleteButton:hover{cursor:pointer}.deletedComponent{background-color:#cccccc}.deletedComponent .editDeleteDiv{text-align:right;font-size:small;margin-right:.5rem;display:none}

