.loginInputArea{max-width:400px;margin:2rem auto}

.signupPageDiv .signupPageBody{padding:2rem;padding-top:75px}@media screen and (max-width: 599px){.signupPageDiv .signupPageBody{padding-top:65px}}.signupPageDiv .signupPageBody p{line-height:1.5}.signupPageDiv .signupPageBody .googleAccount,.signupPageDiv .signupPageBody .pleaseSignup{font-weight:700;text-decoration:underline}.signupPageDiv .signupPageBody .googleAccount:hover,.signupPageDiv .signupPageBody .pleaseSignup:hover{cursor:pointer}

