.pageFooter{padding:1rem;padding-top:0;font-size:small;z-index:5;display:grid;grid-template-columns:1fr 1fr;align-items:center}.pageFooter .nameAndCopyDiv{justify-self:left;text-align:left}.pageFooter .authorName{margin:0;margin-bottom:.1rem;font-size:.7rem}.pageFooter .footerCopyright{margin:0}.pageFooter .contactList{list-style-type:none;padding-left:0;margin:0;display:flex;justify-content:flex-end;align-self:flex-end}.pageFooter a{color:#100c08;font-size:24px;transition:ease .2s}.pageFooter a:hover{opacity:.6}.pageFooter li{margin:0 .5rem}.pageFooter .footerCopyright{font-size:smaller}

